import axios from 'axios';

export function getForecast(cityKey = '2459115') {
    return axios('https://query.yahooapis.com/v1/public/yql', {
        params: {
            format: 'json',
            q: `select * from weather.forecast where woeid=${cityKey}`
        }
    });
};



