<template>
    <transition
        name="slide-down">
        <header class="app-header-wrapper" v-show="show">
            <div class="app-header-left">
                <v-btn
                    icon
                    v-if="showMenu"
                    @click.native="handleClick('menu')">
                    <v-icon class="app-header-icon">menu</v-icon>
                </v-btn>
                <v-btn
                    icon
                    v-if="showBack"
                    @click.native="handleClick('back')">
                    <v-icon class="app-header-icon">arrow_back</v-icon>
                </v-btn>
                <div v-if="showLogo" @click="handleClick('logo')">
                    <slot name="logo">
                        <icon :name="logoIcon" class="app-header-icon"></icon>
                    </slot>
                </div>
            </div>
            <div class="app-header-middle" v-cloak>
                {{ title }}
            </div>
            <div class="app-header-right">
                <slot name="actions"
                    v-for="action, actionIdx in actions"
                    :icon="action.icon"
                    :route="action.route">
                    <v-btn
                        icon="icon"
                        @click.native="handleClick('action', {actionIdx, route: action.route})">
                        <icon v-if="action.svg" :name="action.svg" class="app-header-icon"></icon>
                        <v-icon v-else-if="action.icon" class="app-header-icon">{{ action.icon }}</v-icon>
                    </v-btn>
                </slot>
            </div>
        </header>
    </transition>
</template>

<script>
import EventBus from '@/event-bus';

export default {
    name: 'appHeader',
    props: {
        show: {
            type: Boolean,
            default: true
        },
        showMenu: {
            type: Boolean,
            default: false
        },
        showBack: {
            type: Boolean,
            default: false
        },
        showLogo: {
            type: Boolean,
            default: false
        },
        logoIcon: {
            type: String
        },
        title: {
            type: String
        },
        actions: {
            type: Array
        },
        loading: {
            type: Boolean
        }
    },
    methods: {
        /**
         * 
         *
         * @param {string} source  menu/logo/action
         * @param {Object} data 
         */
        handleClick(source, {actionIdx, route} = {}) {
            // 
            if (this.loading) {
                return;
            }
            let eventData = {};
            // 
            if (source === 'action') {
                eventData.actionIdx = actionIdx;
            }
            // 
            this.$emit(`click-${source}`, eventData);
            // 
            EventBus.$emit(`app-header:click-${source}`, eventData);
            // 
            if (route) {
                this.$router.push(route);
            }
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    
    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    
        $material-custom := {
            bg-color: #fff
            fg-color: #000
            text-color: #000
            primary-text-percent: 0.87
            secondary-text-percent: 0.54
            disabledORhints-text-percent: 0.38
            divider-percent: 0.12
            active-icon-percent: 0.54
            inactive-icon-percent: 0.38
        }
        $material-theme := $material-custom
    


$btn-color = #fff

.app-header-wrapper
    display flex
    justify-content space-between
    align-items center
    height $app-header-height
    background: $theme.primary
    color $btn-color
    padding 0
    box-shadow 0 2px 4px -1px rgba(0,0,0,.2), 0 4px 5px rgba(0,0,0,.14), 0 1px 10px rgba(0,0,0,.12)
    transition transform 0.3s ease-out

    &.slide-down-enter,
    &.slide-down-leave-to
        transform translate(0, -100%)

    & > div
        display flex
        align-items center

    .app-header-middle
        flex 1
        font-size 1.2em

    //  icon 
    .app-header-icon
        color $btn-color
        width 20px
        height 20px

</style>



