import Vue from 'vue';
import Vuex from 'vuex';
import {store as appShell} from '@/app-shells';
import weather from './modules/weather';

Vue.use(Vuex);

export default new Vuex.Store({
    getters: {},
    modules: {
        appShell,
        weather
    }
});



