import * as types from '../mutation-custom-types';
import API from '@/api';

export default {
    state: {
        forecastList: []
    },
    getters: {
        forecastList: state => state.forecastList
    },
    actions: {
        async getForecast({commit}, cityKey) {
            try {
                let queryData = await API.getForecast(cityKey);
                commit(types.SET_FORECAST, queryData.query.results.channel);
            }
            catch(e) {}
        }
    },
    mutations: {
        [types.SET_FORECAST] (state, forecast) {
            state.forecastList.push(forecast);
        }
    }
};



